h2 {
  transform: translateX(-55px);
  opacity: 0;
}

.fact section p {
  opacity: 0;
}

.work picture {
  opacity: 0;
  transition: opacity 0.5s cubic-bezier(0.17, 0.67, 0.36, 1);
}

.slide-up .picture-container {
  transform: translateY(200px);
  opacity: 0;
}

.scroll-down-indicator {
  opacity: 0;
  transition: opacity 2s cubic-bezier(0.17, 0.67, 0.36, 1);
}

.bar > div.width {
  width: 0;
}

#h-tag {
  transition: opacity 0.5s cubic-bezier(0.17, 0.67, 0.36, 1);
}

header h1 {
  opacity: 0;
}

.toAnim {
  opacity: 0;
  transform: translateX(10vh);
}
@media (min-width: 768px) {
  .toAnim {
    transform: translateX(10vw);
  }
}